body {
    font-size: 18px;
    font-family: "Geometria", sans-serif;
}

a {
    text-decoration: none;
    color: inherit;
}

#radio1,
#radio2,
#radio3,
#radio4,
#radio5,
#radio6 {
    display: none;
}

.bg-wrapper {
    background-color: #282828;
    height: 100%;
}

.content-wrapper {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    padding: 70px 90px;
    height: 100vh;
}

.main,
.classes,
.trial,
.trial-two,
.treners,
.abonements,
.schedule,
.reviews,
.contacts {
    min-height: 100vh;
    margin-bottom: 120px;
    background-color: $main-bg-color;
}

.contacts {
    margin-bottom: 0;
}

.circle {
    position: absolute;
    right: 20px;
    bottom: calc(50% - 20px);
    width: 40px;
    height: 40px;
    background-color: #fbf5f4;
    border-radius: 50%;
    cursor: pointer;

    &::before {
        content: "+";
        position: absolute;
        left: 11px; 
        font-size: 28px;
        line-height: 40px;
        font-weight: 400;
        color: #d5d0cf;
        background-color: transparent;
       
    }
}

.circle--active {
    background-color: #f78e6a;
    background-image: linear-gradient(to right, rgba(247, 142, 106, 0) 0%, #fc66a2 100%);

    &::before {
    color: #fff;
    }
}
