.menu {
    position: absolute;
    z-index: 150;
    padding-left: 50px;

    &__list {
        padding: 6px 20px;
        position: relative;
        z-index: 1000;
        font-size: 16px;
        color: rgba($color: #000, $alpha: 0.5);
        visibility: hidden;
        opacity: 0;
        transition: 0.25s visibility, 0.25s opacity;
        background-color: #fbf5f4;
        
    }

    &__item {
        margin: 12px 0;
    }

    &__link {
        &:hover {
            color: rgba($color: #000, $alpha: 1);
        }
        &.active {
            color: rgba($color: #000, $alpha: 1);
        }
    }

    &__close {
        content: "";
        position: absolute;
        top: 9px;
        left: 8px;
        display: inline-block;
        width: 24px;
        height: 24px;
        cursor: pointer;

        &::before,
        &::after {
            content: "";
            position: absolute;
            display: block;
            width: 24px;
            height: 2px;
            background-color: #f78e6a;
        }

        &::before {
            top: 11px;
            right: -2px;
            transform: rotate(45deg);
        }

        &::after {
            top: 11px;
            right: -2px;
            transform: rotate(-45deg);
        }
    }
    &__open {
        content: "";
        position: absolute;
        top: 9px;
        left: 8px;
        display: inline-block;
        width: 24px;
        height: 24px;
        cursor: pointer;

        &::before,
        &::after {
            content: "";
            position: absolute;
            display: block;
            width: 24px;
            height: 2px;
            background-color: #f78e6a;
        }

        &::before {
            top: 11px;
            right: -2px;
        }

        &::after {
            top: 11px;
            right: -2px;
        }
    }
}

.show {
    visibility: visible;
    opacity: 1;
}
