.btn {
    position: relative;
    display: inline-block;
    padding: 20px 50px 20px 80px;
    box-shadow: 11px 11px 50px rgba(0, 0, 0, 0.1);
    border-radius: 29px;
    font-size: 16px;
    font-family: Geometria, sans-serif;
    color: #fff;
    background-color: #f78e6a;
    border: none;
    cursor: pointer;

    &::before {
        content: "+";
        position: absolute;
        top: 0;
        left: 0;
        display: inline-block;
        width: 60px;
        height: 60px;
        font-size: 36px;
        line-height: 60px;
        text-align: center;
        font-weight: 200;
        background-color: #f78e6a;
        background-image: linear-gradient(to right, rgba(247, 142, 106, 0) 0%, #fc66a2 100%);
        border-radius: 50%;
    }

    &:hover {
        background-color: #fff;
        background-image: linear-gradient(to right, #f78e6a 0%, #fc66a2 100%);
        -webkit-background-clip: text;
        background-clip: text;
        -webkit-text-fill-color: transparent;
        color: #fff;
    }
}

.btn--mail {
    &::before {
        content: "";
    }

    &::after {
        content: "";
        position: absolute;
        top: 23px;
        left: 21px;
        display: block;
        width: 18px;
        height: 14px;
        background-image: url("../img/icons/mail.png");
        background-repeat: no-repeat;
        background-position: center center;
    }
}

.btn--plus {
    &::before {
        content: "+";
    }
}

.btn--checked {
    &::before {
        content: "";
    }

    &::after {
        content: "";
        position: absolute;
        top: 24px;
        left: 24px;
        display: block;
        width: 13px;
        height: 10px;
        background-image: url("../img/icons/checked.png");
        background-repeat: no-repeat;
        background-position: center center;
    }
}
