.header {
    display: flex;
    justify-content: space-between;

    &__text {
        position: relative;
        left: calc(50% - 90px);
        line-height: 36px;

        &::before {
            content: "";
            position: relative;
            top: -3px;
            left: -8px;
            display: inline-block;
            width: 6px;
            height: 6px;
            background-color: #f78e6a;
            border-radius: 50%;
        }
    }

    &__tel {
        line-height: 36px;
        font-family: Montserrat, sans-serif;
    }
}
