.abonements {
    &-bg {
        background-image: url("../img/bg/05.png");
        background-repeat: no-repeat;
        background-size: 21vw;
        background-position: 210px center;
    }

    &__btn {
        margin: 20px 0;
    }

    &-content {
        position: relative;
        height: 100vh;

        &__title-inner {
            margin-right: 65px;
            position: absolute;
            top: 10%;
            left: 44.5%;
            max-width: 564px;
        }
    }

    &__wrapper {
        margin-bottom: 20px;
        position: relative;
        z-index: 10;

        .row {
            display: flex;
        }
    }

    &__item {
        width: 280px;
        margin: 0 4px 4px 0;
    }

    & .item-abonements {
        position: relative;
        padding: 30px 0 30px 30px;
        background-color: #fff;

        &__title {
            margin-bottom: 8px;
            font-weight: 400;
        }

        &__price {
            font-weight: 300;

            span {
                font-size: 24px;
                font-weight: 700;
                color: #f78e6a;
                -webkit-background-clip: text;
                background-clip: text;
                -webkit-text-fill-color: transparent;
                background-image: $main-gradient;
            }
        }
    }
}

.circle--active {
    background-color: #f78e6a;
    background-image: linear-gradient(to right, rgba(247, 142, 106, 0) 0%, #fc66a2 100%);

    &::before {
        color: #fff;
    }
}
