.main {
    &-bg {
        background-image: url("../img/bg/01.png");
        background-repeat: no-repeat;
        background-size: 23vw;
        background-position: 170px center;
    }

    &-content {
        position: relative;
        height: 100vh;

        &__logo {
            position: absolute;
            top: 50%;
            transform: translateY(-50%);
        }

        &__title-inner {
            position: absolute;
            top: 50%;
            right: 18%;
            transform: translateY(-50%);
        }

        &__title {
            position: relative;
            background-image: $main-gradient;
            -webkit-background-clip: text;
            background-clip: text;
            -webkit-text-fill-color: transparent;
            font-size: 12vw;
            color: #f78e6a;
            font-weight: 500;
        }

        &__small-title {
            position: absolute;
            top: 42%;
            left: -13%;
            font-size: 1.45vw;
            background-image: $main-gradient;
            -webkit-background-clip: text;
            background-clip: text;
            -webkit-text-fill-color: transparent;
            transform: rotate(-90deg);
        }

        &__desc {
            position: relative;
            max-width: 410px;
            padding-left: 75px;
            line-height: 30px;

            &::before {
                content: "+";
                position: absolute;
                top: calc(50% - 30px);
                left: -12px;
                display: inline-block;
                width: 60px;
                height: 60px;
                font-size: 36px;
                line-height: 60px;
                text-align: center;
                font-weight: 200;
                color: #fff;
                background-color: #f78e6a;
                background-image: linear-gradient(to top, rgba(247, 142, 106, 0) 0%, #fc66a2 100%);
                border-radius: 50%;
            }
        }

        &__pagination {
            position: absolute;
            top: 50%;
            right: 0;
            transform: translateY(-50%);
        }

        & .pagination {
            background-image: $main-gradient;
            text-align: right;
            font-weight: 300;
            -webkit-background-clip: text;
            background-clip: text;
            -webkit-text-fill-color: transparent;
            color: #f88d6c;
        }

        & .pagination--big {
            font-size: 72px;
        }

        & .pagination--small {
            padding-right: 5px;
            font-size: 30px;
        }
    }
}
