.main-form {
    width: 100%;

    &__input {
        display: block;
        width: 430px;
        margin: 16px 0;
        padding: 18px 0;
        background: none;
        border: none;
        border-bottom: 1px solid rgba($color: #000, $alpha: 0.2);
        outline: none;

        &::placeholder {
            color: rgba($color: #000, $alpha: 0.2);
        }

        &:focus {
            border-bottom: 1px solid #000;
            color: #000;
        }

        &:focus::placeholder {
            color: #000;
        }
    }

    &__text {
        max-width: 300px;
        font-size: 12px;
        font-weight: 200;
        line-height: 16px;
        color: #000;

        a {
            font-weight: 400;
        }
    }
}
