.footer {
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.footer-social {
    font-size: 10px;
    font-family: Montserrat, sans-serif;
    text-transform: uppercase;

    &__link {
        margin-right: 30px;
        line-height: 24px;
        color: inherit;

        &:last-child {
            margin-right: 0;
        }

        &:hover {
            color: #f88d6b;
        }
    }
}

.footer-link {
    margin-right: 40px;
    font-size: 16px;
    line-height: 24px;

    &__down {
        position: relative;
        
        &::before {
            content: "";
            position: absolute;
            top: 7px;
            right: -28px;
            z-index: 10;
            display: inline-block;
            width: 6px;
            height: 6px;
            background-image: url("../img/icons/arrow.png");
            background-repeat: no-repeat;
            background-position: center center;
        }

        &:hover:before {
            background-image: url("../img/icons/arrow-hover.png");
        }

        &:after {
            content: "";
            position: absolute;
            top: calc(50% - 15px);
            margin-left: 10px;
            display: inline-block;
            width: 30px;
            height: 30px;
            background-color: #f78e6a;
            background-image: linear-gradient(to top, rgba(247, 142, 106, 0) 0%, #fc66a2 100%);
            border-radius: 50%;
        }

        &:hover:after {
            background-color: #ffffff;
            background-image: none;
            border: 1px solid #fcdbdf;
        }
    }
}
