.trial {
    &-bg {
        background-image: url("../img/bg/03.png");
        background-repeat: no-repeat;
        background-size: 25vw;
        background-position: 150px center;
    }

    &__btn {
        margin: 20px 0;
        &::before {
            content: "";
        }

        &::after {
            content: "";
            position: absolute;
            top: 23px;
            left: 21px;
            display: block;
            width: 18px;
            height: 14px;
            background-image: url("../img/icons/mail.png");
            background-repeat: no-repeat;
            background-position: center center;
        }
    }

    &-content {
        position: relative;
        height: 100vh;

        &__title-inner {
            margin-right: 95px;
            position: absolute;
            top: 10%;
            left: 44.5%;
            max-width: 490px;
        }

        &__title {
            display: inline-block;
            background-image: $main-gradient;
            -webkit-background-clip: text;
            background-clip: text;
            -webkit-text-fill-color: transparent;
            font-size: 80px;
            font-weight: 300;
            color: #f78e6a;
        }

        &__small-title {
            padding-left: 4px;
            font-size: 48px;
            font-weight: 300;
            line-height: 30px;
            color: #000;
        }

        &__desc {
            margin-top: 24px;
            font-weight: 300;
            line-height: 30px;
            color: #000;
        }
    }
}
