.classes {
    &-bg {
        background-image: url("../img/bg/02.png");
        background-repeat: no-repeat;
        background-size: 14vw;
        background-position: 210px center;
    }

    &__btn {
        margin: 16px 0;
    }

    &-content {
        position: relative;
        height: 100vh;

        &__title-inner {
            position: absolute;
            top: 10%;
            left: 44.5%;
            max-width: 490px;
        }

        &__title {
            margin-bottom: 8px;
            background-image: $main-gradient;
            -webkit-background-clip: text;
            background-clip: text;
            -webkit-text-fill-color: transparent;
            font-size: 48px;
            color: #f78e6a;
            font-weight: 400;
        }

        &__small-title {
            margin-bottom: 24px;
            font-size: 24px;
            line-height: 36px;
            color: #000;
        }

        &__desc {
            margin-bottom: 12px;
            font-weight: 300;
            line-height: 30px;
            color: #000;
        }
    }
}
