.teachers {
    &-bg {
        background-image: url("../img/teacher/maksim.png");
        background-repeat: no-repeat;
        background-size: 15vw;
        background-position: 235px center;
    }

    &__btn {
        margin: 20px 0;
    }

    &-content {
        position: relative;
        height: 100vh;

        &__title-inner {
            margin-right: 95px;
            position: absolute;
            top: 10%;
            left: 44.5%;
            max-width: 490px;
        }

        &__title {
           
            font-size: 48px;
        }

        &__small-title {
            margin: 12px 0;
            font-size: 24px;
            line-height: 36px;
        }

        &__desc {
            margin: 24px 0;
            font-weight: 300;
            line-height: 30px;
            color: #000;
        }
    }
}
